import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import ContainedButtons from '../Selection Resources/ContainedButtons';

const useStyles = makeStyles({
  table: {
    minWidth: 300,
  },
});

function createData(calories, fat, carbs) {
  return { calories, fat, carbs };
}

const rows = [
  createData('High Definition Pack', 'Xstream Basic' , 'Xstream Premium'),
  createData(1500, 2498, 3298),
  createData( 262, 16.0, 24),
  createData( 305, 3.7, 67),
  createData( 356, 16.0, 49),
  createData( 'Channel Pack', 'Channel Pack','Channel Pack'),
  createData(<ContainedButtons/>, <ContainedButtons/>,<ContainedButtons/>),
];

export default function BasicTable() {
  const classes = useStyles();

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell align="left"></TableCell>
            <TableCell align="left"></TableCell>
            <TableCell align="left"></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.name}>
              <TableCell align="center">{row.calories}</TableCell>
              <TableCell align="center">{row.fat}</TableCell>
              <TableCell align="center">{row.carbs}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
